.cl-navigation-panel {
    width: 250px;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding-top: 60px;
    background: #f3f3f3;
}

.cl-navigation-panel__item {
    width: 200px;
    height: 30px;
    display: flex;
    align-items: center;
    padding-left: 16px;
    background: #dddddd;

    font-size: 13px;

    border-radius: 16px;

    color: #000000;
    text-decoration: none;

    margin-bottom: 15px;
}

.cl-navigation-panel__item._selected {
    background: #dddddd69;
}

.cl-navigation-panel__item:hover {
    background: #dddddd99;
}

@media screen and (max-width: 800px)  {
    .cl-navigation-panel {
        width: 80px;
    }

    .cl-navigation-panel__item {
        width: 40px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
