.cl-button {

    width: 120px;
    height: 30px;
}

.cl-button__input {
    cursor: pointer;
    width: 100%;
    height: 100%;
    border: 1px solid #0068FF99;
    border-radius: 4px;
    background: #0068FF;
    outline: none;

    color: #FFFFFF;
    font-weight: 700;
}

.cl-button__input:hover {
    background: #0068FF99;
}

.cl-button__input:active {
    background: #0068FFb9;
}
