.cl-cryptologic {
    display: flex;
    flex-direction: column;
    margin: 16px;
}

.cl-cryptologic__title {
    font-size: 32px;
    font-weight: 700;
    margin-bottom: 24px;
}

.cl-cryptologic__inputs {
    display: flex;
    flex-wrap: wrap;
}

.cl-cryptologic__results {
    margin-top: 16px;
    display: flex;
    flex-direction: column;
}

.cl-cryptologic__input {
    margin: 0 8px 16px 0;
}
