.cl-main-page {
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: hidden;
}

.cl-main-page__header {
    height: 60px;
    width: 100%;
    background: #0068FF;
}

.cl-main-page__body {
    display: flex;
    height: 100%;
    background: #f3f3f3;
}

.cl-main-page__navigation-panel {
    height: 92%;
    width: auto;
    overflow-y: scroll;
}

.cl-main-page__content {
    width: 100%;
    heigth: 100%;
    overflow-y: scroll;
}

.cl-main-page__home, .cl-main-page__about {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #000000;
    font-size: 32px;
}
