.cl-text-field__wrapper {
    width: fit-content;
}

.cl-text-field__input:active,
.cl-text-field__input:focus {
    border-bottom: 2px solid #cfb719;

}

.cl-text-field__input {
    border: none;
    border-bottom: 1px solid #a0a0a0;
    outline: none;
    background: none;
    padding: 4px;
}
